import {
  isIdentifierStart,
  isIdentifierContinue,
  isHexDigit,
} from './Lexer.mjs';

const isSyntaxCharacter = (c) => '^$.*+?()[]{}|'.includes(c);
const isClosingSyntaxCharacter = (c) => ')]}|'.includes(c);
const isDecimalDigit = (c) => /[0123456789]/u.test(c);

export class RegExpParser {
  constructor(source, plusU) {
    this.source = source;
    this.position = 0;
    this.plusU = plusU;
    this.capturingGroups = [];
    this.groupSpecifiers = new Map();
  }

  peek() {
    return this.source[this.position];
  }

  test(c) {
    return this.source[this.position] === c;
  }

  eat(c) {
    if (this.test(c)) {
      this.next();
      return true;
    }
    return false;
  }

  next() {
    const c = this.source[this.position];
    this.position += 1;
    return c;
  }

  expect(c) {
    if (!this.eat(c)) {
      throw new SyntaxError(`Expected ${c} but got ${this.peek()}`);
    }
  }

  // Pattern ::
  //   Disjunction
  parsePattern() {
    const node = {
      type: 'Pattern',
      groupSpecifiers: this.groupSpecifiers,
      capturingGroups: this.capturingGroups,
      Disjunction: undefined,
    };
    node.Disjunction = this.parseDisjunction();
    return node;
  }

  // Disjunction ::
  //   Alternative
  //   Alternative `|` Disjunction
  parseDisjunction() {
    const node = {
      type: 'Disjunction',
      AlternativeList: [],
    };
    do {
      node.AlternativeList.push(this.parseAlternative());
    } while (this.eat('|'));
    return node;
  }


  // Alternative ::
  //   [empty]
  //   Term Alternative
  parseAlternative() {
    const node = {
      type: 'Alternative',
      TermList: [],
    };
    while (this.position < this.source.length
           && !isClosingSyntaxCharacter(this.peek())) {
      node.TermList.push(this.parseTerm());
    }
    return node;
  }

  // Term ::
  //   Assertion
  //   Atom
  //   Atom Quantifier
  parseTerm() {
    const assertion = this.maybeParseAssertion();
    if (assertion) {
      return assertion;
    }
    return {
      type: 'Term',
      capturingParenthesesBefore: this.capturingGroups.length,
      Atom: this.parseAtom(),
      Quantifier: this.maybeParseQuantifier(),
    };
  }

  // Assertion ::
  //   `^`
  //   `$`
  //   `\` `b`
  //   `\` `B`
  //   `(` `?` `=` Disjunction `)`
  //   `(` `?` `!` Disjunction `)`
  //   `(` `?` `<=` Disjunction `)`
  //   `(` `?` `<!` Disjunction `)`
  maybeParseAssertion() {
    if (this.eat('^')) {
      return { type: 'Assertion', subtype: '^' };
    }
    if (this.eat('$')) {
      return { type: 'Assertion', subtype: '$' };
    }

    const look2 = this.source.slice(this.position, this.position + 2);
    if (look2 === '\\b') {
      this.position += 2;
      return { type: 'Assertion', subtype: 'b' };
    }
    if (look2 === '\\B') {
      this.position += 2;
      return { type: 'Assertion', subtype: 'B' };
    }

    const look3 = this.source.slice(this.position, this.position + 3);
    if (look3 === '(?=') {
      this.position += 3;
      const d = this.parseDisjunction();
      this.expect(')');
      return {
        type: 'Assertion',
        subtype: '?=',
        Disjunction: d,
      };
    }
    if (look3 === '(?!') {
      this.position += 3;
      const d = this.parseDisjunction();
      this.expect(')');
      return {
        type: 'Assertion',
        subtype: '?!',
        Disjunction: d,
      };
    }

    const look4 = this.source.slice(this.position, this.position + 4);
    if (look4 === '(?<=') {
      this.position += 4;
      const d = this.parseDisjunction();
      this.expect(')');
      return {
        type: 'Assertion',
        subtype: '?<=',
        Disjunction: d,
      };
    }
    if (look4 === '(?<!') {
      this.position += 4;
      const d = this.parseDisjunction();
      this.expect(')');
      return {
        type: 'Assertion',
        subtype: '?<!',
        Disjunction: d,
      };
    }

    return undefined;
  }

  // Quantifier ::
  //   QuantifierPrefix
  //   QuantifierPrefix `?`
  // QuantifierPrefix ::
  //   `*`
  //   `+`
  //   `?`
  //   `{` DecimalDigits `}`
  //   `{` DecimalDigits `,` `}`
  //   `{` DecimalDigits `,` DecimalDigits `}`
  maybeParseQuantifier() {
    let QuantifierPrefix;

    if (this.eat('*')) {
      QuantifierPrefix = '*';
    } else if (this.eat('+')) {
      QuantifierPrefix = '+';
    } else if (this.eat('?')) {
      QuantifierPrefix = '?';
    } else if (this.eat('{')) {
      QuantifierPrefix = {
        DecimalDigits_a: undefined,
        DecimalDigits_b: undefined,
      };
      QuantifierPrefix.DecimalDigits_a = Number.parseInt(this.parseDecimalDigits(), 10);
      if (this.eat(',')) {
        if (!this.test('}')) {
          QuantifierPrefix.DecimalDigits_b = Number.parseInt(this.parseDecimalDigits(), 10);
          if (QuantifierPrefix.DecimalDigits_a > QuantifierPrefix.DecimalDigits_b) {
            throw new SyntaxError('Numbers out of order in {} quantifier');
          }
        }
      }
      this.expect('}');
    }

    if (QuantifierPrefix) {
      return {
        type: 'Quantifier',
        QuantifierPrefix,
        greedy: !this.eat('?'),
      };
    }

    return undefined;
  }

  // Atom ::
  //   PatternCharacter
  //   `.`
  //   `\` AtomEscape
  //   CharacterClass
  //   `(` GroupSpecifier Disjunction `)`
  //   `(` `?` `:` Disjunction `)`
  parseAtom() {
    if (this.eat('.')) {
      return { type: 'Atom', subtype: '.', enclosedCapturingParentheses: 0 };
    }
    if (this.eat('\\')) {
      return this.parseAtomEscape();
    }
    if (this.eat('(')) {
      const node = {
        type: 'Atom',
        capturingParenthesesBefore: this.capturingGroups.length,
        enclosedCapturingParentheses: 0,
        capturing: true,
        GroupSpecifier: undefined,
        Disjunction: undefined,
      };
      if (this.eat('?')) {
        if (this.eat(':')) {
          node.capturing = false;
        } else {
          this.expect('<');
          node.GroupSpecifier = this.parseIdentifierName();
          this.expect('>');
        }
      }
      if (node.capturing) {
        this.capturingGroups.push(node);
      }
      if (node.GroupSpecifier) {
        this.groupSpecifiers.set(node.GroupSpecifier, node.capturingParenthesesBefore);
      }
      node.Disjunction = this.parseDisjunction();
      this.expect(')');
      node.enclosedCapturingParentheses = this.capturingGroups.length - node.capturingParenthesesBefore - 1;
      return node;
    }
    if (this.test('[')) {
      return this.parseCharacterClass();
    }
    if (isSyntaxCharacter(this.peek())) {
      throw new SyntaxError(`Expected a PatternCharacter but got ${this.peek()}`);
    }
    return {
      type: 'Atom',
      PatternCharacter: this.next(),
    };
  }

  // AtomEscape ::
  //   DecimalEscape
  //   CharacterClassEscape
  //   CharacterEscape
  //   `k` GroupName
  parseAtomEscape() {
    if (this.eat('k')) {
      return {
        type: 'AtomEscape',
        GroupName: this.parseGroupName(),
      };
    }
    const CharacterClassEscape = this.maybeParseCharacterClassEscape();
    if (CharacterClassEscape) {
      return {
        type: 'AtomEscape',
        CharacterClassEscape,
      };
    }
    const DecimalEscape = this.maybeParseDecimalEscape();
    if (DecimalEscape) {
      return {
        type: 'AtomEscape',
        DecimalEscape,
      };
    }
    return {
      type: 'AtomEscape',
      CharacterEscape: this.parseCharacterEscape(),
    };
  }

  // CharacterEscape ::
  //   ControlEscape
  //   `c` ControlLetter
  //   `0` [lookahead  DecimalDigit]
  //   HexEscapeSequence
  //   RegExpUnicodeEscapeSequence
  //   IdentityEscape
  parseCharacterEscape() {
    switch (this.peek()) {
      case 'f':
      case 'n':
      case 'r':
      case 't':
      case 'v':
        return {
          type: 'ControlEscape',
          subtype: this.next(),
        };
      case 'c': {
        this.next();
        const c = this.next();
        const p = c.codePointAt(0);
        if ((p >= 65 && p <= 90) || (p >= 97 && p <= 122)) {
          return {
            type: 'CharacterEscape',
            ControlLetter: c,
          };
        }
        return {
          type: 'CharacterEscape',
          IdentityEscape: c,
        };
      }
      case 'x':
        this.next();
        if (isHexDigit(this.source[this.position]) && isHexDigit(this.source[this.position + 1])) {
          return {
            type: 'HexEscapeSequence',
            HexDigit_a: this.next(),
            HexDigit_b: this.next(),
          };
        }
        return {
          type: 'CharacterEscape',
          IdentityEscape: 'x',
        };
      case 'u':
        this.next();
        if (isHexDigit(this.source[this.position])
            && isHexDigit(this.source[this.position + 1])
            && isHexDigit(this.source[this.position + 2])
            && isHexDigit(this.source[this.position + 3])) {
          return {
            type: 'RegExpUnicodeEscapeSequence',
            Hex4Digits: {
              HexDigit_a: this.next(),
              HexDigit_b: this.next(),
              HexDigit_c: this.next(),
              HexDigit_d: this.next(),
            },
          };
        }
        return {
          type: 'CharacterEscape',
          IdentityEscape: 'u',
        };
      default: {
        const c = this.next();
        if (c === '0' && !isDecimalDigit(this.peek())) {
          return {
            type: 'CharacterEscape',
            subtype: '0',
          };
        }
        return {
          type: 'CharacterEscape',
          IdentityEscape: c,
        };
      }
    }
  }

  // DecimalEscape ::
  //   NonZeroDigit DecimalDigits? [lookahead != DecimalDigit]
  maybeParseDecimalEscape() {
    return undefined;
  }

  // CharacterClassEscape ::
  //   `d`
  //   `D`
  //   `s`
  //   `S`
  //   `w`
  //   `W`
  //   [+U] `p{` UnicodePropertyValueExpression `}`
  //   [+U] `P{` UnicodePropertyValueExpression `}`
  maybeParseCharacterClassEscape() {
    switch (this.peek()) {
      case 'd':
      case 'D':
      case 's':
      case 'S':
      case 'w':
      case 'W':
        return {
          type: 'CharacterClassEscape',
          value: this.next(),
        };
      default:
        return undefined;
    }
  }

  // CharacterClass ::
  //   `[` ClassRanges `]`
  //   `[` `^` ClassRanges `]`
  parseCharacterClass() {
    this.expect('[');
    const node = {
      type: 'CharacterClass',
      invert: false,
      ClassRanges: undefined,
    };
    node.invert = this.eat('^');
    node.ClassRanges = this.parseClassRanges();
    this.expect(']');
    return node;
  }

  // ClassRanges ::
  //   [empty]
  //   NonemptyClassRanges
  parseClassRanges() {
    const ranges = [];
    while (!this.test(']')) {
      if (this.position >= this.source.length) {
        throw new SyntaxError('Unexpected end of CharacterClass');
      }
      const atom = this.parseClassAtom();
      if (this.eat('-')) {
        const atom2 = this.parseClassAtom();
        if (atom > atom2) {
          throw new SyntaxError(`${atom}-${atom2} is not a valid class range`);
        }
        ranges.push([atom, atom2]);
      } else {
        ranges.push(atom);
      }
    }
    return ranges;
  }

  // ClassAtom ::
  //   `-`
  //   ClassAtomNoDash
  // ClassAtomNoDash ::
  //   SourceCharacter but not one of `\` or `]` or `-`
  //   `\` ClassEscape
  // ClassEscape :
  //   `b`
  //   [+U] `-`
  //   CharacterClassEscape
  //   CharacterEscape
  parseClassAtom() {
    if (this.eat('\\')) {
      if (this.eat('b')) {
        return {
          type: 'ClassEscape',
          value: 'b',
        };
      }
      if (this.eat('-')) {
        return {
          type: 'ClassEscape',
          value: '-',
        };
      }
      const CharacterClassEscape = this.maybeParseCharacterClassEscape();
      if (CharacterClassEscape) {
        return CharacterClassEscape;
      }
      return this.parseCharacterEscape();
    }
    return this.next();
  }

  // RegExpIdentifierName ::
  //   RegExpIdentifierStart
  //   RegExpIdentifierName RegExpIdentifierPart
  parseIdentifierName() {
    let name = '';
    while (true) {
      const c = this.peek();
      if (isIdentifierStart(c) || isIdentifierContinue(c) || c === '$') {
        name += this.next();
      } else {
        break;
      }
    }
    return name;
  }

  // DecimalDigits ::
  //   DecimalDigit
  //   DecimalDigits DecimalDigit
  parseDecimalDigits() {
    let n = '';
    while (isDecimalDigit(this.peek())) {
      n += this.next();
    }
    return n;
  }
}
